import React, { useEffect, useState } from 'react'
import { theme } from '../../configtheme'

let defaultStyle ={
    backgroundColor:theme.palette.valen.secondary,
    boxShadow:`${theme.palette.valen.main} 4px 4px 0px`,
    borderRadius:'8px',
    transition: 'trasnform 200ms,box-shadow 200ms',
    width:'100%',
    height:'2rem',
    color:'white',
    cursor:'pointer',
    "&:hover":{
        color: "red !important",
    }
}

export const CustomButton = ({ onClick, text, type, style, ...props }) => {

    return (
        <button style={defaultStyle}  onClick={onClick} type={type} {...props}  >
           {text}
        </button>
    )
}
