import { Avatar, Box, Button, Grid, Link, TextField, Typography } from '@mui/material'
import LockOutlinedIcon from '@mui/icons-material/LockOutlined';
import React from 'react'
import { useNavigate } from 'react-router';
import { theme } from '../../configtheme';
import { NormalButton } from '../buttons/NormalButton';
import { CustomButton } from '../buttons/CustomButton';
import * as Yup from 'yup';
import { useDispatch } from 'react-redux';
import { Form, Formik } from 'formik';
import { InputText } from '../inputs/InputText';
import { authLogin } from '../../redux/actions/auth.actions';
import ErrorAlert from '../Alerts/ErrorAlert';
import { Loading } from '../Loadings/Loading';


const initialValues = {
  email: '',
  password: '',
}

const validation = Yup.object({
  email: Yup.string().email('Email Invalido').required('Requerido'),
  password: Yup.string().min(3, 'Debe tener al menos 3 caracteres').required('Requerido'),

})

export const Login = () => {

  const dispatch = useDispatch();
  const navigate = useNavigate();
  const handleSubmit = (data) => {
    dispatch(authLogin(data));
  };
  const GotoRegister = () => {
    navigate("/register");
  }
  return (
    <>
      <Avatar sx={{ m: 1, bgcolor: theme.palette.valen.secondary }}>
        <LockOutlinedIcon />
      </Avatar>
      <Typography component="h1" variant="h5">
        Bienvenido
      </Typography>
      <Formik initialValues={initialValues}
        onSubmit={(values) => {
          // console.log(values);
          handleSubmit(values);
        }}
        validationSchema={validation}
      >
        {({ isValid }) => (
          <Form component="form"  autoComplete="on" noValidate style={{ marginTop: '5px' }}>
            <InputText
              required
              fullWidth
              id="email"
              type="email"
              label="Email"
              name="email"
              autoComplete="email"
            />
            <InputText
              required
              fullWidth
              name="password"
              label="Password"
              type="password"
              id="password"
              autoComplete="new-password"
            />
            <NormalButton text="Iniciar Sesion" type="submit" />
            <Grid container>
              <Grid item xs={12} md={6} >
                <Link variant="body2">
                  Olvido su contraseña?
                </Link>
              </Grid>
              <Grid item xs={12} md={6}>
                <Link onClick={GotoRegister} variant="body2">
                  {"No tiene cuenta? Registrese"}
                </Link>
              </Grid>
            </Grid>
          </Form>
        )}
      </Formik>
      <Loading />
      <ErrorAlert />
    </>
  )
}
